module.exports = "import { Component, ViewEncapsulation } from '@angular/core';\n\n@Component({\n  selector     : 'nz-demo-badge-change',\n  encapsulation: ViewEncapsulation.None,\n  template     : `\n    <div>\n      <nz-badge [nzCount]=\"count\">\n        <a class=\"head-example\"></a>\n      </nz-badge>\n      <nz-button-group>\n        <button nz-button (click)=\"minCount()\"><i nz-icon type=\"minus\"></i></button>\n        <button nz-button (click)=\"addCount()\"><i nz-icon type=\"plus\"></i></button>\n      </nz-button-group>\n    </div>\n\n    <div style=\"margin-top: 10px;\">\n      <nz-badge [nzDot]=\"dot\">\n        <a class=\"head-example\"></a>\n      </nz-badge>\n      <nz-switch [(ngModel)]=\"dot\"></nz-switch>\n    </div>\n  `,\n  styles       : [ `\n    .ant-badge:not(.ant-badge-status) {\n      margin-right: 20px;\n    }\n\n    .head-example {\n      width: 42px;\n      height: 42px;\n      border-radius: 4px;\n      background: #eee;\n      display: inline-block;\n    }\n  ` ]\n})\nexport class NzDemoBadgeChangeComponent {\n\n  count = 5;\n  dot = true;\n\n  addCount(): void {\n    this.count++;\n  }\n\n  minCount(): void {\n    this.count--;\n    if (this.count < 0) {\n      this.count = 0;\n    }\n  }\n}\n"